<template>
	<div class="glossary" :class="{ '_opened': isOpened }">
		<div class="close" @click="toggleVisible">
			<icon icon="times" />
		</div>

		<div class="glossary__title color-beige strong small mb-2">
			Быстрый словарь
		</div>

		<div class="glossary__search" v-if="currentNotion === null">
			<div class="glossary__input">
				<input type="text" v-model="value" placeholder="Введите название">
			</div>

			<div class="glossary__result tb-scroll">
				<div class="glossary__result-item" v-for="notion in searchResults" :key="notion.id" @click="openNotion(notion)">
					{{ notion.title }}
				</div>
			</div>
		</div>

		<div class="glossary__card tb-scroll" v-else>
			<div class="glossary__card-close" @click="currentNotion = null">
				<icon icon="external-link-square-alt" />
			</div>

			<div class="glossary__card-title color-beige strong small">
				{{ currentNotion.title }}
			</div>

			<div class="glossary__card-text mb-2">
				{{ currentNotion.text }}
			</div>

			<div class="glossary__card-poster">
				<img :src="currentNotion.poster" :alt="currentNotion.title">
			</div>
		</div>

	</div>
</template>

<script>
	import { mapGetters } from "vuex";

	export default {
		name: "Glossary",

		data() {
			return {
				isOpened: false,
				value: '',
				currentNotion: null,
			}
		},

		methods: {
			toggleVisible() {
				this.isOpened = !this.isOpened;
			},
			openNotion(notion) {
				this.currentNotion = notion;
			},
		},

		computed: {
			searchResults() {
				return this.value
					? this.notions.filter(notion => notion.title.toLowerCase().indexOf(this.value.toLowerCase()) !== -1)
					: [];
			},
			...mapGetters([
				'notions'
			])
		},

		created() {
			app.$glossary = this;
		},
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.glossary {
		display: flex;
		flex-direction: column;
		position: absolute;
		right: -330px;
		bottom: 10px;
		z-index: 1;
		height: 400px;
		width: 300px;
		background-color: @color-dark;
		border-radius: 5px;
		transition: right .3s ease-in-out;
		-webkit-box-shadow: inset 0 0 0 1px black;
		-moz-box-shadow: inset 0 -1px 0 1px black;
		box-shadow: inset 0 0 0 1px @color-beige;
		padding: 15px 15px 0;

		&._opened {
			right: 1rem;
		}

		&__input {
			margin-bottom: 0.5rem;
			padding-bottom: 0.5rem;
			border-bottom: dotted 1px @color-beige;
		}

		&__result, &__card {
			max-height: 297px;
			overflow: auto;
		}

		&__result-item {
			color: @color-beige;
			margin-bottom: 10px;
			font-weight: bold;
			font-size: 14px;
			cursor: pointer;
		}

		&__card {
			&-close {
				cursor: pointer;

				&:hover {
					color: @color-pink
				}
			}

			&-title {
				padding: 1rem 0;
			}

			&-text {
				font-size: 14px;
				line-height: 1.6;
				padding-right: 15px;
			}

			&-poster {
				img {
					max-width: 100%;
				}
			}
		}

		.close {
			position: absolute;
			top: 10px;
			right: 10px;
			font-size: 14px;
			cursor: pointer;
			
			&:hover {
				color: @color-pink;
			}
		}
	}
</style>


